import {addaddress} from "../../apis"
export default {
	data() {
		return {
			token:"",
			addressData: {
				name: '',
				mobile: '',
				addressName: '请选择',
				address: '',
				procode:'',
				citycode:''
			}
		}
	},
	onLoad() {
		try {
		    const value = uni.getStorageSync('token');
		    if (value) {
		        this.token=value.token
		    }
		} catch (e) {
		    // error
		}	
	},
	methods: {
		chooseLocation(){
			uni.chooseLocation({
				success: (data)=> {
					this.addressData.addressName = data.name;
					this.addressData.address = data.name;
				}
			})
		},
		confirm(){
			let data = this.addressData;
			if(!data.name){
				uni.showToast({
					title:"姓名不能为空",
					icon:"none"
				})
				return;
			}
			if(!/(^1[3|4|5|7|8][0-9]{9}$)/.test(data.mobile)){
				uni.showToast({
					title:"请输入正确的手机号",
					icon:"none"
				})
				return;
			}
			if(!data.address){
				uni.showToast({
					title:"请在地图上选择你的位置",
					icon:"none"
				})
				return;
			}
			if(!data.procode){
				uni.showToast({
					title:"请输入所属省份编码",
					icon:"none"
				})
				return;
			}
			if(!data.citycode){
				uni.showToast({
					title:"请输入所属城市编码",
					icon:"none"
				})
				return;
			}
			addaddress({
				address:data.address,
				cityId:data.citycode,
				linkMan:data.name,
				mobile:data.mobile,
				provinceId:data.procode,
				token:this.token
			}).then(res=>{
				let [err,data]=res
				console.log(data.data) 
				uni.showToast({
					title:"添加成功"
				})
				uni.navigateTo({
					url:"../address/address"
				})
			})
		}
	}
}
